@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
/* ALL */
body {
  margin: 0;
  height: 100%;
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html{
  height: 100%;
}

#root{
  height: 100%;
}

.app{
  height: 100%;
}

main{
  height: 100%;
}

/* Font styles */
h1{
  color: rgba(0, 0, 0, 0.87);
  font-weight: 700;
  font-size: 38px;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

h2{
  font-size: 24px;
  color: rgba(0, 0, 0, 0.87);
}

p{
  font-size: 18px;
  color: rgba(0, 0, 0, 0.7);
}

/* Buttons */
button{
  font-family: 'Karla', sans-serif !important;
}

/* HOME */ /*TODO* BYT UT BILD*/
.home{
  background-image: url(/FrolichConsulting/static/media/background.1e0b19a0.png);
  height: 100%;
  text-align: center;
}

.home h1{
  color: white;
}

/* About */
.about{
  height: 100%;
  width: 70%;
  margin: 0 auto;
}

.work{
  width: 70%;
  margin: 0 auto;
}

.contact{
  width: 70%;
  margin: 0 auto;
}

/* Utils */
.center-vertical{
  width: 50%;
  margin: 0 auto;
}

.center-horizontal{
  padding-top: 50vh;
}

.bottom-aligned{
  position: absolute !important;
  bottom: 10px;
}

.flex{
  display: flex;
}

.align-items{
  align-items: center;
}

.ml-1{
  margin-left: 1em;
}

.ml-2{
  margin-left: 2em;
}

.hamburgerButton{
  display: flex;
  flex-direction: column;
  height: 24px;
  width: 30px;
  background: transparent;
  justify-content: space-around;
  border: none;
  cursor: pointer;
  padding: 0;
}

.hamburgerButton:focus{
  outline: none;
}

.hamburgerButton-line{
  height: 3px;
  width: 30px;
  background: white;
}

@media (min-width: 769px){
  .hamburgerMenu-button{
    display: none !important;
  }
}

.navbar{
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 56px;
}

.navbar-navigation{
  margin-top: 20px;
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.navbar-logo{
  margin-right: auto;
  margin-left: 1rem;
}

.navbar-logo img{
  heigh: 80%;
  width: 80%;
}

.navbar-navigation ul{
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-navigation-items li{
  padding: 0 1.5rem;
}

.navbar-navigation-items a{
  color: white;
  text-decoration: none;
}

.navbar-navigation-items a:hover{
  font-weight: 700;
}

/*TODO*/
.navbar-navigation-items a:active{
}

@media (max-width: 768px){
  .navbar-navigation-items{
    display: none;
  }
}

@media (min-width: 769px){
  .navbar-logo{
    margin-left: 0;
  }
}

.drawer{
  height: 100%;
  background: white;
  box-shadow: 3px 0px 5px rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 0;
  left: 0;
  width: 400px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  transition: -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0), -webkit-transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
}

.drawer ul{
  list-style: none;
  display: flex;
  flex-direction: column;
}

/*TODO: DEF BLACKS*/
.drawer a{
  text-decoration: none;
  color: black;
}

.drawer li{
  margin: 0.5rem 0;
}

.drawer.open{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

@media (min-width: 769px){
  .drawer{
    display: none;
  }
}

.backdrop{
  top: 0;
  left: 0;
  z-index: 100;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}

@media (min-width: 769px){
  .backdrop{
    display: none;
  }
}

